import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'fml-simple-game',
  templateUrl: './simple-game.component.html',
  styleUrls: ['./simple-game.component.css']
})
export class SimpleGameComponent implements OnInit {

  private _xMin: number = 2;
  private _yMin: number = 2;
  private _xMax: number = 515;
  private _yMax: number = 515;

  private _xPos: number;
  private _yPos: number;

  private _xInc: number = 0;
  private _yInc: number = 0;

  private _movementStarted: boolean = false;
  private _movementPaused: boolean = false;

  private _backgroundColor: string = 'red';
  private _targetStyle: any;

  public foundTarget: number;
  public hit: number;
  public miss: number;

  constructor() {
  }

  TargetStyle() {
    this._targetStyle = { 
      "top": `${this._yPos}px`, 
      "left": `${this._xPos}px`, 
      "background": this._backgroundColor 
    }
    return this._targetStyle;
  }

  OnMouseEvent($event) {
    let childEvent = $event.path[0];

    if(childEvent.id=="card" && $event.type == "click"){
      this.miss += 1;
    }

    if(childEvent.id=="target"){
      this.TargetEvent($event.type);
    }
  }

  OnKeyEvent($event) {
    let key = $event.key;
    switch(key){
      case "4": {
        this._movementPaused = false;
        this._xInc += -0.2;        
        break;
      }
      case "6": {
        this._movementPaused = false;
        this._xInc += 0.2;
        break;
      }
      case "2": {
        this._movementPaused = false;
        this._yInc += 0.2;
        break;
      }
      case "8": {
        this._movementPaused = false;
        this._yInc += -0.2;
        break;
      }
      case "5": {
        if(this._movementPaused){ 
          this._movementPaused = false 
        }else{
          this._movementPaused = true;
          if(this.hit < 5){
            this._xInc = 0;
            this._yInc = 0;
          }
        }
        break;
      }
    }
    if(!this._movementStarted){
      this.StartMovement();
      this._movementStarted = true;
    }
  }

  private TargetEvent(eventType) {
    switch(eventType){
      case "click": {
        this.hit += 1;
        this.SetNewCoordinates();

        if(this.hit >= 5) {
          this._movementPaused = false;
          if(this._xInc == 0){
            this._xInc = 1;
          }
          if(this._yInc == 0) {
            this._yInc = 1;
          }
          if(!this._movementStarted) { 
            this.StartMovement();
            this._movementStarted = true;
          }
        }
        break;
      }
      case "mouseenter": {
        this._backgroundColor = "blue";
        this.foundTarget += 1;
        break;
      }
      case "mouseleave": {
        this._backgroundColor = "red";
        break;
      }
    }
  }

  private StartMovement() {
      setInterval(() => {
        this.frame();
      }, 1 * 60);
  }

  private frame() {
    if(!this._movementStarted || this._movementPaused){ return };

    console.log('frame');
    if (this._xPos >= this._xMax || this._xPos <= this._xMin) {
      this._xInc = -this._xInc;
    }    
    if (this._yPos >= this._yMax || this._yPos <= this._yMin) {
      this._yInc = -this._yInc;
    }    
    this._xPos += this._xInc; 
    this._yPos += this._yInc;
  }

  private SetNewCoordinates() {
    this._yPos = this.GenerateRandomValue(1, this._xMax);
    this._xPos = this.GenerateRandomValue(1, this._xMax);
  }

  private GenerateRandomValue(min: number, max: number) {
    return Math.floor(Math.random() * max) + min;
  }

  ngOnInit() {
    this.SetNewCoordinates();
    this.foundTarget = 0;
    this.hit = 0;
    this.miss = 0;    
  }
}
